import { useState, useEffect, useRef } from 'react';
import { sendMessage, getMessageHistory, ChatMessage } from '@/services/chatService';
import { marked } from 'marked';
import { supabase } from "@/integrations/supabase/client";

export const useVoiceChat = () => {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [userInput, setUserInput] = useState("");
  const messageEndRef = useRef<HTMLDivElement | null>(null);

  // Load chat history -       15  25
  const loadChatHistory = async () => {
    const history = await getMessageHistory(25);
    setMessages(history);
  };

  // Handle sending a text message
  const handleSendMessage = () => {
    if (userInput.trim()) {
      handleSendMessageText(userInput);
      setUserInput("");
    }
  };

  // Common function for sending messages - optimized for faster responses
  const handleSendMessageText = async (text: string) => {
    if (!text.trim() || isLoading) return;
    
    setIsLoading(true);
    
    // Check for confirmation messages for booking cancellation
    if (isConfirmationMessage(text)) {
      await handlePendingActions(text);
      setIsLoading(false);
      return;
    }
    
    // Add user message to history with loading flag
    const tempMessage: ChatMessage = {
      user_query: text,
      bot_response: "",
      isLoading: true
    };
    
    setMessages(prev => [...prev, tempMessage]);
    
    try {
      // Send message and get response
      const response = await sendMessage(text);
      
      if (response) {
        // Immediately update message history with received response
        setMessages(prev => prev.filter(msg => !msg.isLoading).concat([response]));
        
        // Check if the response contains a booking cancellation or WhatsApp-related request
        await checkAndHandleSpecialRequests(response.bot_response, text);
      } else {
        // If no response received, remove loading flag but keep user message
        setMessages(prev => prev.map(msg => 
          msg.isLoading ? { ...msg, isLoading: false, bot_response: "   " } : msg
        ));
      }
    } catch (error) {
      console.error("   :", error);
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { ...msg, isLoading: false, bot_response: " " } : msg
      ));
    } finally {
      setIsLoading(false);
    }
  };
  
  //     
  const isConfirmationMessage = (text: string): boolean => {
    const confirmationKeywords = [
      '', '', '', '', '', 
      '', '', '', '', ''
    ];
    
    return confirmationKeywords.some(keyword => 
      text.toLowerCase().includes(keyword)
    );
  };
  
  //      
  const handlePendingActions = async (text: string) => {
    //   3       
    const recentMessages = messages.slice(-3);
    const cancellationRequest = recentMessages.find(msg => 
      msg.bot_response && (
        msg.bot_response.toLowerCase().includes('    ') ||
        msg.bot_response.toLowerCase().includes('  ')
      )
    );
    
    if (cancellationRequest) {
      //     
      const botResponse = cancellationRequest.bot_response;
      const guestNameMatch = botResponse.match(/\s+(\w+\s+\w+|\w+)/i);
      const guestName = guestNameMatch ? guestNameMatch[1] : null;
      
      if (guestName && isConfirmMessage(text)) {
        //    
        const confirmMsg: ChatMessage = {
          user_query: text,
          bot_response: "",
          isLoading: true
        };
        
        setMessages(prev => [...prev, confirmMsg]);
        
        try {
          //    
          const { data: bookingData, error } = await supabase
            .from('bookings')
            .select('*')
            .ilike('guest_name', `%${guestName}%`)
            .eq('status', 'confirmed')
            .limit(1)
            .single();
            
          if (error) {
            throw new Error(`    : ${error.message}`);
          }
          
          if (bookingData) {
            //  
            await manageBooking('delete', { id: bookingData.id, guest_name: bookingData.guest_name });
            
            //    
            setMessages(prev => prev.map(msg => 
              msg.isLoading ? { 
                ...msg, 
                isLoading: false, 
                bot_response: `   ${bookingData.guest_name}  !` 
              } : msg
            ));
          } else {
            //    
            setMessages(prev => prev.map(msg => 
              msg.isLoading ? { 
                ...msg, 
                isLoading: false, 
                bot_response: `       ${guestName}` 
              } : msg
            ));
          }
        } catch (error) {
          console.error("   :", error);
          //    
          setMessages(prev => prev.map(msg => 
            msg.isLoading ? { 
              ...msg, 
              isLoading: false, 
              bot_response: `    : ${error.message}` 
            } : msg
          ));
        }
        return true;
      }
    }
    
    return false;
  };
  
  //    
  const isConfirmMessage = (text: string): boolean => {
    const confirmWords = ['', '', '', '', ''];
    return confirmWords.some(word => text.toLowerCase().includes(word));
  };
  
  //       
  const checkAndHandleSpecialRequests = async (botResponse: string, userQuery: string) => {
    //      (   GPT)
    const isCancellationRequest = userQuery.toLowerCase().includes(' ') || 
                                 userQuery.toLowerCase().includes(' ') ||
                                 userQuery.toLowerCase().includes(' ');
    
    //     WhatsApp 
    const isWhatsAppViewRequest = userQuery.toLowerCase().includes(' ') || 
                                userQuery.toLowerCase().includes('  ') ||
                                userQuery.toLowerCase().includes(' ') ||
                                userQuery.toLowerCase().includes(' ');
    
    if (isWhatsAppViewRequest && botResponse.includes('') && botResponse.includes('')) {
      //    GPT,    
      console.log("    WhatsApp   GPT");
    }
    
    //     
    if (isCancellationRequest && botResponse.includes('')) {
      console.log("     ");
      //       
    }
  };
  
  //     WhatsApp
  const getWhatsAppMessages = async (params: {
    guestName?: string,
    phoneNumber?: string,
    onlyUnread?: boolean,
    limit?: number
  }) => {
    try {
      setIsLoading(true);
      
      //      
      let query = supabase.from('whatsapp_messages').select('*');
      
      //    ,  
      if (params.guestName) {
        query = query.ilike('guest_name', `%${params.guestName}%`);
      }
      
      //    ,  
      if (params.phoneNumber) {
        query = query.eq('phone_number', params.phoneNumber);
      }
      
      //   ,  
      if (params.onlyUnread) {
        query = query.eq('is_read', false);
      }
      
      //     ( )
      query = query.order('created_at', { ascending: false });
      
      //   
      query = query.limit(params.limit || 20);
      
      //  
      const { data: messages, error } = await query;
      
      if (error) {
        throw new Error(`    WhatsApp: ${error.message}`);
      }
      
      //   ,    
      if (messages && messages.length > 0) {
        //        
        const formattedResponse = formatWhatsAppMessages(messages, params);
        
        //    
        const responseMessage: ChatMessage = {
          user_query: ` ${params.guestName ? `  ${params.guestName}` : ''}${params.onlyUnread ? ' ()' : ''}`,
          bot_response: formattedResponse,
          isLoading: false
        };
        
        setMessages(prev => [...prev, responseMessage]);
        
        //     ,    
        if (params.onlyUnread) {
          const messageIds = messages.map(msg => msg.id);
          await supabase
            .from('whatsapp_messages')
            .update({ is_read: true })
            .in('id', messageIds);
        }
        
        return messages;
      } else {
        //    
        const noMessagesResponse: ChatMessage = {
          user_query: ` ${params.guestName ? `  ${params.guestName}` : ''}${params.onlyUnread ? ' ()' : ''}`,
          bot_response: ` ${params.guestName ? `  ${params.guestName}` : ''}${params.onlyUnread ? ' ()' : ''}  .`,
          isLoading: false
        };
        
        setMessages(prev => [...prev, noMessagesResponse]);
        return [];
      }
    } catch (error) {
      console.error("    WhatsApp:", error);
      
      //    
      const errorMessage: ChatMessage = {
        user_query: ` ${params.guestName ? `  ${params.guestName}` : ''}`,
        bot_response: `    : ${error.message}`,
        isLoading: false
      };
      
      setMessages(prev => [...prev, errorMessage]);
      return [];
    } finally {
      setIsLoading(false);
    }
  };
  
  //     WhatsApp
  const formatWhatsAppMessages = (messages: any[], params: any): string => {
    if (!messages || messages.length === 0) {
      return "  ";
    }
    
    let result = "";
    
    //     
    if (params.guestName) {
      result += ` ** ${params.onlyUnread ? ' ' : ''} ${params.guestName}**\n\n`;
    } else if (params.onlyUnread) {
      result += ` ** **\n\n`;
    } else {
      result += ` **  WhatsApp**\n\n`;
    }
    
    //     / 
    const groupedMessages: {[key: string]: any[]} = {};
    
    messages.forEach(msg => {
      const key = msg.guest_name || msg.phone_number;
      if (!groupedMessages[key]) {
        groupedMessages[key] = [];
      }
      groupedMessages[key].push(msg);
    });
    
    //        
    Object.keys(groupedMessages).forEach(key => {
      if (Object.keys(groupedMessages).length > 1) {
        result += `### ${key}\n\n`;
      }
      
      //     
      const sortedMessages = groupedMessages[key].sort((a, b) => 
        new Date(a.created_at).getTime() - new Date(b.created_at).getTime()
      );
      
      sortedMessages.forEach(msg => {
        const date = new Date(msg.created_at);
        const formattedDate = `${date.toLocaleDateString('ru-RU')} ${date.toLocaleTimeString('ru-RU')}`;
        const direction = msg.is_incoming ? "" : "";
        const status = !msg.is_read && msg.is_incoming ? " ()" : "";
        
        result += `${direction} **${formattedDate}**${status}:\n${msg.content}\n\n`;
      });
    });
    
    return result;
  };

  //         WhatsApp 
  const checkAndHandleWhatsAppRequest = (botResponse: string, userQuery: string) => {
    const whatsappKeywords = [' ', ' whatsapp', ' whatsapp', 
                             ' whatsapp', ' ', ' '];
    
    // ,         WhatsApp
    const isWhatsAppRequest = whatsappKeywords.some(keyword => userQuery.toLowerCase().includes(keyword));
    
    //     WhatsApp       -    
    if (isWhatsAppRequest && botResponse.includes('') && botResponse.includes('')) {
      console.log("    WhatsApp:", userQuery);
      //        
    }
  };

  // Create a task from the assistant -     
  const createTask = async (details: {
    title: string,
    description: string,
    priority?: 'high' | 'medium' | 'low',
    houseNumber?: string
  }) => {
    try {
      console.log("  :", details);
      setIsLoading(true);
      
      // Add temporary message about task creation
      const tempMessage: ChatMessage = {
        user_query: ` : ${details.title}`,
        bot_response: " ...",
        isLoading: true
      };
      
      setMessages(prev => [...prev, tempMessage]);
      
      // Get the current session token for authentication
      const { data: { session } } = await supabase.auth.getSession();
      const authToken = session?.access_token;
      
      console.log("   Edge Function task-manager");
      
      // Send task creation request to the edge function with auth token if available
      const headers: HeadersInit = {
        'Content-Type': 'application/json'
      };
      
      if (authToken) {
        console.log("    ");
        headers['Authorization'] = `Bearer ${authToken}`;
      } else {
        console.log("   ,   ");
      }
      
      const response = await fetch('https://fwchkusgryebslnjkoly.supabase.co/functions/v1/task-manager', {
        method: 'POST',
        headers,
        body: JSON.stringify({
          action: 'create',
          task: {
            title: details.title,
            description: details.description,
            priority: details.priority || 'medium',
            house_number: details.houseNumber || '1'
          }
        })
      });
      
      console.log("   Edge Function, :", response.status);
      
      if (!response.ok) {
        const errorText = await response.text();
        console.error(`   : ${response.status}`, errorText);
        throw new Error(`   : ${response.status} - ${errorText}`);
      }
      
      const result = await response.json();
      console.log("  :", result);
      
      // Update message with success response
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `  "${details.title}"  !${result.task?.id ? ` ID: ${result.task.id}` : ''}${result.employee ? `,  : ${result.employee}` : ''}` 
        } : msg
      ));
      
    } catch (error) {
      console.error("   :", error);
      
      // Update message with error information
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `    : ${error.message}` 
        } : msg
      ));
    } finally {
      setIsLoading(false);
    }
  };

  // Send WhatsApp message
  const sendWhatsAppMessage = async (phoneNumber: string, message: string) => {
    try {
      setIsLoading(true);
      
      // Add temporary message about WhatsApp sending
      const tempMessage: ChatMessage = {
        user_query: ` WhatsApp  ${phoneNumber}: ${message}`,
        bot_response: " ...",
        isLoading: true
      };
      
      setMessages(prev => [...prev, tempMessage]);
      
      // Send WhatsApp message request to the edge function
      const response = await fetch('https://fwchkusgryebslnjkoly.supabase.co/functions/v1/whatsapp-sender', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          phone: phoneNumber,
          message: message
        })
      });
      
      if (!response.ok) {
        throw new Error(`   WhatsApp: ${response.status}`);
      }
      
      const result = await response.json();
      
      // Update message with success response
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `  WhatsApp     ${phoneNumber}` 
        } : msg
      ));
      
    } catch (error) {
      console.error("   WhatsApp:", error);
      
      // Update message with error information
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `    WhatsApp: ${error.message}` 
        } : msg
      ));
    } finally {
      setIsLoading(false);
    }
  };

  // Manage bookings
  const manageBooking = async (action: 'create' | 'update' | 'delete', bookingData: any) => {
    try {
      setIsLoading(true);
      
      let actionText = action === 'create' ? '' : action === 'update' ? '' : '';
      
      // Add temporary message about booking management
      const tempMessage: ChatMessage = {
        user_query: `${actionText} : ${bookingData.guest_name || ''}, : ${bookingData.start_date || ' '} - ${bookingData.end_date || ' '}`,
        bot_response: `${actionText} ...`,
        isLoading: true
      };
      
      setMessages(prev => [...prev, tempMessage]);
      
      // Send booking management request to the edge function
      const response = await fetch('https://fwchkusgryebslnjkoly.supabase.co/functions/v1/booking-manager', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          action,
          booking: bookingData,
          bookingId: bookingData.id //    update  delete
        })
      });
      
      if (!response.ok) {
        throw new Error(`    : ${response.status}`);
      }
      
      const result = await response.json();
      
      // Map actions to success messages
      const successMessages = {
        'create': `   ${bookingData.guest_name}  !`,
        'update': `   ${bookingData.guest_name}  !`,
        'delete': `   ${bookingData.guest_name}  !`
      };
      
      // Update message with success response
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: successMessages[action] 
        } : msg
      ));
      
    } catch (error) {
      console.error("    :", error);
      
      // Update message with error information
      setMessages(prev => prev.map(msg => 
        msg.isLoading ? { 
          ...msg, 
          isLoading: false, 
          bot_response: `      : ${error.message}` 
        } : msg
      ));
    } finally {
      setIsLoading(false);
    }
  };

  return {
    messages,
    isLoading,
    userInput,
    setUserInput,
    loadChatHistory,
    handleSendMessage,
    handleSendMessageText,
    createTask,
    sendWhatsAppMessage,
    manageBooking,
    getWhatsAppMessages,
  };
};
