
import { supabase } from "@/integrations/supabase/client";
import { toast } from "@/components/ui/sonner";

//    
export interface ChatMessage {
  id?: string;
  timestamp?: string;
  user_query: string;
  bot_response: string;
  isLoading?: boolean;
}

//       
export const sendMessage = async (userMessage: string): Promise<ChatMessage | null> => {
  try {
    console.log(" :", userMessage);

    //     conversations
    const { data, error } = await supabase
      .from('conversations')
      .insert([{ user_query: userMessage, bot_response: "" }])
      .select()
      .single();

    if (error) {
      console.error("   :", error);
      toast.error("   ");
      return null;
    }

    let response;
    
    try {
      //  yandex-gpt-proxy       GPT
      response = await Promise.race([
        supabase.functions.invoke('yandex-gpt-proxy', {
          body: {
            message: userMessage,
            conversationId: data.id
          }
        }),
        // 15-     (  10  15, ..    )
        new Promise((_, reject) => 
          setTimeout(() => reject(new Error("    ")), 15000)
        )
      ]);
    } catch (err) {
      console.error("  :", err);
      toast.error("   ,    ");
      
      //         
      await supabase
        .from('conversations')
        .update({ bot_response: "  . ,   ." })
        .eq('id', data.id);
        
      //      
      return {
        ...data,
        bot_response: "  . ,   ."
      };
    }

    if (response.error) {
      console.error("  :", response.error);
      toast.error("    ");
      
      //          webhook
      await supabase
        .from('conversations')
        .update({ bot_response: "    ." })
        .eq('id', data.id);
      
      return {
        ...data,
        bot_response: "    ."
      };
    }

    //         
    const { data: updatedData, error: fetchError } = await supabase
      .from('conversations')
      .select()
      .eq('id', data.id)
      .single();

    if (fetchError) {
      console.error("   :", fetchError);
      toast.error("   ");
      return data; //   
    }

    //       ,     
    if (response?.data?.bot_response) {
      console.log("    :", response.data.bot_response);
      return {
        ...updatedData,
        bot_response: response.data.bot_response
      };
    }

    //   ,   
    if (!updatedData.bot_response) {
      console.log("   ");
      
      //          
      await supabase
        .from('conversations')
        .update({ bot_response: "    . ,    -." })
        .eq('id', data.id);
      
      return {
        ...updatedData,
        bot_response: "    . ,    -."
      };
    }

    console.log(" :", updatedData);
    return updatedData;
  } catch (err) {
    console.error(" :", err);
    toast.error("    ");
    return null;
  }
};

//     
export const getMessageHistory = async (limit = 25): Promise<ChatMessage[]> => {
  try {
    const { data, error } = await supabase
      .from('conversations')
      .select('*')
      .order('timestamp', { ascending: false })
      .limit(limit);

    if (error) {
      console.error("    :", error);
      toast.error("    ");
      return [];
    }

    return data.reverse();
  } catch (err) {
    console.error("    :", err);
    toast.error("    ");
    return [];
  }
};
